@import '../styles/utils.scss';

.SassComponent {
    display: flex;
    justify-content: center;
    .box { // 일반 CSS에서는 .SassComponent의 .box와 마찬가지
        background:red;
        cursor: pointer;
        transition: all 0.3 ease-in;
        &.red {
            // .red 클래스가 .box와 함께 사용되었을 때
            background: $red;
            @include square(1);
        }
        &.orange {
            background: $orange;
            @include square(2);
        }
        &.yellow {
            background: $yellow;
            @include square(3);
        }
        &.green {
            background: $green;
            @include square(4);
        }
        &.blue {
            background: $blue;
            @include square(5);
        }
        &.indigo {
            background: $indigo;
            @include square(6);
        }
        &.violet {
            background: $violet;
            @include square(7);
        }
        &.hover {
            // .box에 마우스를 올렸을 때
            background: black;
        }
    }
}